@import "../../common/styles/variables";

.header {
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  right: 0;
  z-index: $z-index-header;
  width: calc(100% - 300px);
  height: 50px;
  box-shadow: 0 2px 10px $color-black-shadow;

  @media (max-width: 1087.9px) {
    width: 100%; } }

.header:before {
  content: '';
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background-color: $color-black-one;
  z-index: 2; }

.header__nav {
  display: flex;
  align-items: center;
  justify-content: space-between; }

.header__menu_big {
  display: flex;
  // position: relative
  justify-content: space-between;
  // align-content: center

  @media (max-width: 767.9px) {
    display: none; } }

